*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.search-form {
  justify-content: center;
  margin-bottom: 24px;
  padding: 8px;
  display: flex;
}

.search-form input {
  width: 300px;
  height: 50px;
  float: left;
  color: #dfe3e8;
  background-color: #173a85;
  border: none;
  border-radius: 5px;
  padding-left: 45px;
  font-size: 10pt;
}

.search-form button {
  color: #dfe3e8;
  background-color: #173a85;
  border-radius: 5px;
  margin-left: 10px;
}

.load-more {
  color: #dfe3e8;
  background-color: #173a85;
  border-radius: 5px;
  margin: auto;
  padding: 8px;
  display: block;
}

.gallery {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 0 auto 30px;
  padding: 0;
  display: grid;
}

.info {
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.gallery img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

